.container {
  width: 50%;
  padding: 3.2rem;
  background-color: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 100;
  min-height: 30rem;
}
.heading {
  text-align: center;
  letter-spacing: 0.1rem;
  text-transform: uppercase;
}
.form-control {
  display: flex;
  flex-wrap: wrap;
  margin-top: 3.2rem;
  gap: 1.6rem;
}
.form-group {
  flex: 0 0 45%;
  margin-bottom: 3rem;
  display: flex;
  flex-direction: column;
  position: relative;
}
.form-input {
  height: 3.2rem;
  padding-left: 0.8rem;
  border: 0.1rem solid #000;
  border-radius: 0.3rem;
}
.form-input:placeholder-shown ~ .form-label {
  transform: translateY(0);
  opacity: 0;
  top: 40%;
}
.form-input:focus {
  border-radius: 0.3rem;
  outline: 3px #c92a2a;
  outline-style: dotted;
  outline-offset: 0.4rem;
  border: none;
}
.form-label {
  position: absolute;
  top: -3rem;
  opacity: 1;
  transition: all 0.3s;
}
.buttons {
  flex: 1;
  display: flex;
  gap: 2.4rem;
}
.input-invalid {
  background-color: #d25757b5;
  border: #c92a2a solid 0.2rem;
}
.texterror {
  margin-top: 1rem;
  color: #c92a2a;
}
.form-invalid,
.form-invalid:hover {
  background-color: #302020;
  color: #fff;
  cursor: auto;
}
